import Vue from 'vue'
import Router from 'vue-router'
//style1
import index_1 from '@/components/style1/index'
import productList_1 from '@/components/style1/productList'
import productDetail_1 from '@/components/style1/productDetail'
import shopCart_1 from '@/components/style1/shopCart'
import activity_1 from '@/components/style1/activity'
import userCenter_1 from '@/components/style1/userCenter'
import swiperShow_1 from '@/components/style1/swiperShow'
import img_text_stairs_1 from '@/components/style1/img_text_stairs'
import big_img_1 from '@/components/style1/big_img'
import edit_list_1 from '@/components/style1/edit_list'
import orderAddress_1 from '@/components/style1/orderAddress'

//style2
import productList_2 from '@/components/style2/productList'
import productDetail_2 from '@/components/style2/productDetail'

//登录注册找回密码通用
import login from '@/components/login'
import findPassword from '@/components/findPassword'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index_1',
      name: 'index_1',
      component: index_1,
      meta: {
        title: '商城首页'
      }
    },
    {
      path: '/edit_list_1',
      name: 'edit_list_1',
      component: edit_list_1,
      meta: {
        title: '编辑系列'
      }
    },
    {
      path: '/swiperShow_1',
      name: 'swiperShow_1',
      component: swiperShow_1,
      meta: {
        title: '哦呦呦轮播图呢'
      }
    },
     {
      path: '/img_text_stairs_1',
      name: 'img_text_stairs_1',
      component: img_text_stairs_1,
      meta: {
        title: '哦呦呦图片+文本呢'
      }
    },
    {
      path: '/big_img_1',
      name: 'big_img_1',
      component: big_img_1,
      meta: {
        title: '哦呦呦大图片呢'
      }
    },
    {
      path: '/productList_1',
      name: 'productList_1',
      component: productList_1,
      meta: {
        title: '商品列表'
      }
    },
    {
      path: '/productDetail_1',
      name: 'productDetail_1',
      component: productDetail_1,
      meta: {
        title: '商品详情'
      }
    },
    {
      path: '/shopCart_1',
      name: 'shopCart_1',
      component: shopCart_1,
      meta: {
        title: '购物车'
      }
    },
    {
      path: '/orderAddress_1',
      name: 'orderAddress_1',
      component: orderAddress_1,
      meta: {
        title: '订单地址'
      }
    },
    {
      path: '/activity_1',
      name: 'activity_1',
      component: activity_1,
      meta: {
        title: '活动页'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录注册'
      }
    },
    {
      path: '/findPassword',
      name: 'findPassword',
      component: findPassword,
      menuShow: true,
      iconCls: 'iconfont icon-books',
      children:[
        {path:'',component:login,name:'login',menuShow:true,meta:{requireAuth: true }}
      ],
      meta: {
        title: '找回密码'
      }
    },
    {
      path: '/userCenter_1',
      name: 'userCenter_1',
      component: userCenter_1,
      meta: {
        title: '个人中心'
      }
    },
    {
      path: '/productList_2',
      name: 'productList_2',
      component: productList_2,
      meta: {
        title:'第二模板产品列表'
      }
    },
    {
      path: '/productDetail_2',
      name: 'productDetail_2',
      component: productDetail_2,
      meta: {
        title:'第二模板产品详情'
      }
    }
  ]
})




