<template>
  <div id="productDetail">
    <div class="product-detail-main clearFloat container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{path: '/productList'}">产品列表</el-breadcrumb-item>
        <el-breadcrumb-item>产品详情</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="product-detail-wrap clearFloat">
        <div class="product-detail-img fl" v-if="type === 'pc'">
          <div class="pic-zoom">
            <pic-zoom :url="url" :scale="3" :big-url="url" :scroll="true"></pic-zoom>
          </div>
          <div class="small-img clearFloat">
            <i class="el-icon-arrow-left arrow-left fl" @click="imgScroll('left')"></i>
            <div class="small-img-wrap fl">
              <ul class="scroll-wrap">
                <li v-for="item in urlList">
                  <img :src="item.smallUrl" :data-big="item.bigUrl" alt="" @mousemove="mouseover(item.bigUrl)" :class="{srcActive:srcActive==item}">
                </li>
              </ul>
            </div>
            <i class="el-icon-arrow-right arrow-right fr" @click="imgScroll('right')"></i>
          </div>
        </div>
        <div class="phone-banner" v-else-if="type === 'phone'">
          <wc-swiper>
            <wc-slide v-for="item in urlList">
              <img :src="item.bigUrl" alt="">
            </wc-slide>
          </wc-swiper>
        </div>
        <div class="fr product-detail">
          <h5 class="product-detail-title mgb20">哈哈哈哈或或或或或或或或或</h5>
          <div class="product-detail-price mgb20">
            <span class="price-text">价格：</span>
            <span class="price">¥ 163</span>
          </div>
          <div class="mgb20">
            <span class="same-title">配送至：</span>
            <span class="address-text" @click="addressWrap = !addressWrap" v-if="address_default.address">{{address_default.address_name}}</span>
            <span class="address-text" @click="addressWrap = !addressWrap" v-else>请选择配送地址</span>
            <span class="same-title" v-show="address_default.address"> > 有货 </span>
            <div class="address_warp" v-show="addressWrap">
              <div class="address_dialog">
                <b class="address_close el-icon-close" @click="addressWrap = !addressWrap"></b>
                <ul class="address_lists clearFloat">
                  <li v-for="addressItem in addressList" class="address_item" @click="addressSelect(addressItem.address_name)" :class="{active : addressActive == addressItem.address_name}">
                    <!--<div class="address_default">默认地址</div>-->
                    <p class="address_detail">{{addressItem.address_name}}</p>
                    <p>{{addressItem.user_phone}}</p>
                    <p>{{addressItem.user_name}}（收）</p>
                    <i>已选中</i>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div class="mgb20">
            <span class="same-title">重量：</span>
            <span class="same-title">2KG</span>
          </div>
          <div class="select-parameter mgb20 clearFloat">
            <span class="same-title fl ">
              选择颜色：
            </span>
            <ul class="clearFloat">
              <li v-for="item in wpList" :key="item.name" :class="{active : paramsActive == item.name}" @click="selected('parameter',item.name)">{{item.name}}</li>
            </ul>
          </div>
          <div class="select-parameter mgb20 clearFloat">
            <span class="same-title fl ">
              size：
            </span>
            <ul class="clearFloat">
              <li v-for="item in szList" :key="item.name" :class="{active : paramsSize == item.name}" @click="selected('size',item.name)">{{item.name}}</li>
            </ul>
          </div>
          <div class="mgb20">
            <span class="same-title fl">选择数量：</span>
            <div>
              <el-input-number v-model="num" :min="1" :max="10" size="mini" label="描述文字"></el-input-number>
            </div>
          </div>
          <div class="btn-wrap">
            <button class="buy-btn">立即购买</button>
            <button class="cat-btn">加入购物车</button>
          </div>
        </div>
      </div>
      <div class="product-introduce">
        <div class="product-introduce-title">
          <ul class="clearFloat">
            <li v-for="item in introduceList" :class="{active:active1==item.name}" :key="item.name" @click="selected('introduce',item.name)">
              {{item.name}}
            </li>
          </ul>
        </div>
      </div>
      <div class="introduce-item">
        <el-collapse accordion>
          <el-collapse-item>
            <template slot="title" class="introduce-list">
              一致性 Consistency<i class="header-icon el-icon-info"></i>
            </template>
            <div>与现实生活一致：与现实生活的流程、逻辑保持一致，遵循用户习惯的语言和概念；</div>
            <div>在界面中一致：所有的元素和结构需保持一致，比如：设计样式、图标和文本、元素的位置等。</div>
          </el-collapse-item>
          <el-collapse-item title="反馈 Feedback">
            <div>控制反馈：通过界面样式和交互动效让用户可以清晰的感知自己的操作；</div>
            <div>页面反馈：操作后，通过页面元素的变化清晰地展现当前状态。</div>
          </el-collapse-item>
          <el-collapse-item title="效率 Efficiency">
            <div>简化流程：设计简洁直观的操作流程；</div>
            <div>清晰明确：语言表达清晰且表意明确，让用户快速理解进而作出决策；</div>
            <div>帮助用户识别：界面简单直白，让用户快速识别而非回忆，减少用户记忆负担。</div>
          </el-collapse-item>
          <el-collapse-item title="可控 Controllability">
            <div>用户决策：根据场景可给予用户操作建议或安全提示，但不能代替用户进行决策；</div>
            <div>结果可控：用户可以自由的进行操作，包括撤销、回退和终止当前操作等。</div>
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import PicZoom from 'vue-piczoom'
  export default {
    components: {
      PicZoom
    },
    data() {
      return {
        paramsActive:'',
        paramsSize:'',
        addressActive:'',
        active1:'商品介绍',
        srcActive :'',
        num: 1,
        tempLength : 0,//临时变量,当前移动的长度
        type: '',
        addressWrap:false,
        postdata:"",
        configs: {
          width:400,
          height:400,
          maskWidth:100,
          maskHeight:100,
          maskColor:'#ddd',
          maskOpacity:0.5
        },
        address_default:{address_name:'',address:false},
        addressList:[{address_name:'广东省深圳市龙华新区民治街道向南新村32栋',user_name:'朱俊业',user_phone:'18512600311'},{address_name:'广东省深圳市龙华新区民治街道向南新村40栋',user_name:'张宗师',user_phone:'18512600311'}],
        introduceList:[{name:'商品介绍'},{name:'商品评价（100）'}],
        wpList: [
          {name: '食品饮料'},
          {name: '鲜花'},
          {name: '蛋糕'},
          {name: '水果生鲜'},
          {name: '服装鞋帽'}
        ],
        szList: [
          {name: '1'},
          {name: '2'},
          {name: '3'},
          {name: '4'},
          {name: '5'},
          {name: '6'}
        ],
        url:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',
        urlList:[
          {smallUrl:'http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg',bigUrl:'http://imgsrc.baidu.com/imgad/pic/item/3801213fb80e7beca9004ec5252eb9389b506b38.jpg'},
          {smallUrl:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg',bigUrl:'https://826327700.github.io/vue-piczoom/dist/static/imac2.jpg'}
        ]
      }
    },
    created: function(){
      this.postdata=this.$route.params.zh_pa_id;
      console.log(this.postdata);
    },
    mounted (){
      this.screenWidth();
    },
    methods: {
      mouseover: function (imgSrc) {
        this.url = imgSrc;
        this.srcActive = imgSrc
      },
      screenWidth: function() {
        if(document.body.clientWidth <= 767){
          this.type = 'phone'
          console.log(this.type)
        }else {
          this.type = 'pc'
        }
      },
      selected(type,name){
        if (type == "parameter") {
          this.paramsActive = name;
        }
        else if (type == "size") {
          this.paramsSize = name;
        }
        else {
          this.paramsActive = name;
          this.paramsSize = name;
        }
      },
      imgScroll: function (type) {
        let viewNum = 5; //设置每次显示图片的个数量
        let moveNum = 2; //每次移动的数量
        let scrollDiv = document.querySelector(".scroll-wrap"); //进行移动动画的容器
        let scrollItems = document.querySelectorAll(".small-img-wrap ul li"); //移动容器里的集合
        let moveLength = 87 * moveNum; //计算每次移动的长度
        let countLength = (scrollItems.length - viewNum) * 87; //计算隐藏li的长度
        let moveLeft = Number(window.getComputedStyle(scrollDiv).left.slice(0,-2));//容器的left值
        if(type == 'right'){
          if(this.tempLength < countLength){
            if((countLength - this.tempLength) > moveLength){
              scrollDiv.style.left = `${moveLeft - moveLength}px`;
              this.tempLength += moveLength;
            }else{
              scrollDiv.style.left = `${moveLeft - (countLength - this.tempLength)}px`;
              this.tempLength = countLength;
            }
          }
        }else {
          if(this.tempLength > 0){
            if(this.tempLength > moveLength){
              scrollDiv.style.left = `${moveLength + moveLeft}px`;
              this.tempLength -= moveLength;
            }else{
              scrollDiv.style.left = `${this.tempLength + moveLeft}px`;
              this.tempLength = 0;
            }
          }
        }
      },
      addressSelect: function (addressName) {
        this.address_default.address_name = addressName;
        this.address_default.address = true;
        this.addressActive = addressName;
        this.addressWrap  = false
      }
    }
  };
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../../common/stylus/general.styl"
  .product-detail-main
    margin-top 20px
    margin-bottom 20px
  .product-detail-wrap
    margin-top 20px
    position relative
  .magnifier-box img
    height 100%
  .pic-zoom
    width 400px
    height 400px
    border 1px solid #ddd
  .small-img
    width 400px
    position relative
    margin-top 15px
  .small-img-wrap
    width 340px
    overflow hidden
    ul
      width 10000px
      overflow hidden
      position relative
      left 0
      transition all .3s ease-in
      li
        float left
        margin-right 10px
        img
          width 77px
          height 77px
          cursor pointer
          box-sizing border-box
        .srcActive
          border 1px solid color
  .arrow-left,.arrow-right
    display block
    font-size 30px
    color color2
    height 80px
    line-height 80px
    cursor pointer
  .product-detail,.product-introduce
    transition all .2s ease-in
  .product-detail-title
    color #333
    font-size 16px
    margin-top 10px
  .product-detail-price
    padding 5px 15px
    background #ddd
    border-radius 2px
    font-size 16px
  .price-text
    color #333
  .price
    color color2
  .address-text
    color color2
    font-size 14px
    cursor pointer
    display inline-block
    max-width 62%
    vertical-align bottom
    overflow hidden
    text-overflow ellipsis
    white-space nowrap
  .address_warp
    position absolute
    z-index 1000
    top 32px
    left 0
  .address_dialog
    width 488px
    position relative
    background-color #fff
    border 1px solid #aaa
    padding 10px
  .address_close
    position absolute
    right 5px
    cursor pointer
    overflow hidden
    font-size 18px
    top 3px
    color #333
  .address_lists
    height 204px
    overflow auto
    margin-right -10px
  .address_item
    width 215px
    height 82px
    margin 10px 10px 0px 0 !important
    display block
    float left
    cursor pointer
    position relative
    overflow hidden
    padding 5px 8px
    font-size 14px
    color #333
    background-image url(../../common/img/select-unti.png)
    background-repeat no-repeat
    background-position: 0 0
    background-size 100% 100%
  .address_item:hover
    background-image url(../../common/img/addree-select.png)
  .address_item:nth-child(2n)
    margin-right 0 !important
  .address_default
    position absolute
    right 0
    top 0
    background-color #ccc
    color #FFF
    padding 0 2px
    opacity .7
    font-size 12px
    line-height 1.5
  .address_item.active
    background-image url(../../common/img/addree-select.png)
  .address_item.active i
    display block
  .address_dialog i
    background-image url(../../common/img/select-right.png)
    position absolute !important
    bottom 0
    right 0
    width 12px
    height 12px
    overflow hidden
    text-indent -99em
    display none
    background-repeat no-repeat
    background-position 0 0
  .address_detail
    line-height 20px
    height 40px
    display -webkit-box
    -webkit-line-clamp 2
    -webkit-box-orient vertical
    overflow hidden
    text-overflow ellipsis
  .same-title
    color #666
    font-size 16px
    display inline-block
    width 85px
    text-align left
  .select-parameter li
    float left
    margin-right 3%
    padding 2% 3%
    font-size 14px
    color #666
    border 1px solid #ddd
    margin-bottom 10px
    cursor pointer
    border-radius 5px
  .select-parameter ul
    width 68%
    float left
  .select-parameter span
    margin-top 10px
  .select-parameter li.active
    background color2
    color #fff
    border-color color2
  .buy-btn,.cat-btn
    padding .2rem .3rem
    font-size fl
    border none
    outline none
    cursor pointer
    margin-top 20px
    border-radius 2px
  .buy-btn
    background color2
    color #fff
    margin-right 20px
  .cat-btn
    background #ddd
    color #333
  .product-introduce-title
    background #ddd
    margin-top 50px
    li
      font-size fl
      padding .2rem .3rem
      cursor pointer
      color #333
      float left
      border none
    li.active
      background color2
      color #fff
/*  .introduce-item>>>.el-collapse-item
    margin 0 5px*/
  /* 小于767px */
  @media screen and (max-width: 767px){
    .product-detail-main{
      padding-bottom 1.3rem
    }
    .btn-wrap{
      text-align center
    }
    .product-detail-price{
      padding-left 0
    }
    .phone-banner img{
      width 100%
    }
    .product-detail-img{
      display none
    }
    .product-detail{
      width 100%
    }
    .address_dialog{
      width 100%
      box-sizing border-box
    }
    .address_close{
      right 0
    }
    .address_item{
      width 42%
    }
  }
</style>



