<template>
  <div id="login">
    <div class="login-main-wrapper">
      <ul class="login-list">
        <li :class="index==loginIndex?'active':''" @click="loginList(item,index)" v-for="(item,index) in loginNav" :key="index">
          {{item}}
        </li>
      </ul>
      <div class="login-main" v-show="login">
        <el-form :model="loginForm" ref="loginForm" :rules="rules">
          <el-form-item prop="email">
            <el-input placeholder="邮箱" v-model="loginForm.email">
              <template slot="prepend">邮箱</template>
            </el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input placeholder="密码" type="password" v-model="loginForm.pass" autocomplete="off">
              <template slot="prepend">密码</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="login-button" :loading="loginLoading" @click="loginBtn('loginForm')" style="width: 100%;background: #42a749;border: none;font-size: .35rem">登录</el-button>
          </el-form-item>
        </el-form>
        <div class="login-foot">
          <router-link to="/findPassword">忘记密码</router-link>
          <span @click="loginIndex=1;login=false;register=true">免费注册</span>
        </div>
        <div class="third-party">
          <div class="third-party-text">可用以下方式登录</div>
          <div class="third-party-btn">
            <ul>
              <li v-for="item in loginUrl">
                <router-link :to="item"></router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="login-main" v-show="register">
        <el-form :model="registerForm" ref="registerForm" :rules="rules">
          <el-form-item prop="email">
            <el-input placeholder="邮箱" v-model="registerForm.email">
              <template slot="prepend">邮箱</template>
            </el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input placeholder="密码" type="password" v-model="registerForm.pass" autocomplete="off">
              <template slot="prepend">密码</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="registerLoading" @click="loginBtn('registerForm')" style="width: 100%;background: #42a749;border: none;font-size: .35rem">注册</el-button>
          </el-form-item>
        </el-form>
        <div class="login-foot">
          <router-link to="/findPassword">忘记密码</router-link>
          <span @click="loginIndex=0;login=true;register=false">前往登录</span>
        </div>
        <div class="third-party">
          <div class="third-party-text">可用以下方式登录</div>
          <div class="third-party-btn">
            <ul>
              <li v-for="item in loginUrl">
                <router-link :to="item"></router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <productFooter></productFooter>
  </div>
</template>

<script type="text/ecmascript-6">
  import productFooter from './style1/footer'
 export default {
    components:{
      productFooter
    },
   data(){
     var validatePass = (rule, value, callback) => {
       if (value === '') {
         callback(new Error('请输入密码'));
       }else {
         callback();
       }
     };
      return{
        loginNav:['登录','注册'],
        loginUrl:['/user/fblogin','/user/ins','/user/tt'],
        loginLoading:false,
        registerLoading:false,
        login:true,
        register:false,
        loginIndex:0,
        loginEmail:'',
        loginPassword:'',
        loginForm: {
          email:'',
          pass:''
        },
        registerForm: {
          email:'',
          pass:''
        },
        rules: {
          email:[
            {
              required: true,
              message: '请输入邮箱地址',
              trigger: 'blur'
          },
            {
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change']
            }
          ],
          pass:[
            {
              validator: validatePass, trigger: 'blur'
            }
          ]
        }
      }
   },
   methods:{
      loginList: function (item,index) {
        this.loginIndex = index;
        if (index == 0){
          this.login = true;
          this.register = false;
        } else {
          this.login = false;
          this.register = true;
        }
      },
     loginBtn: function (login) {
       this.$refs[login].validate((valid) => {
         if (valid) {
           alert('submit!');
         } else {
           console.log('error submit!!');
           return false;
         }
       });
     }
   }
 }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  #login
    position relative
    top 0
    right 0
    left 0
    bottom 0
    padding-bottom 400px
    background url(../common/img/background.jpg) no-repeat;
    background-size 100% 100%
  .login-main-wrapper
    width 500px
    height 400px
    border 1px solid #ddd
    top 80px
    background rgba(250,250,250,.6)
    .login-list
      width 100%
      height 50px
      margin-bottom 30px
      li
        float left
        height 100%
        width 50%
        box-sizing border-box
        background #eee
        border 1px solid #ddd
        cursor pointer
        text-align center
        font-size fl
        color #666
        line-height 50px
        font-weight bold
      li.active
        background #fff
        color color
        border none
  .login-main
    width 80%
    margin 0 auto
  .login-btn>button
    width 100%
    background color
    font-size fl
    color #fff
    height 40px
    line-height 40px
    text-align center
    border-radius 4px
    outline none
    border none
    cursor pointer
  .login-btn>button:hover
    opacity 0.9
  .login-foot
    font-size fm
    margin-top 20px
    text-align center
    span
      color #666
      margin-left 50px
      cursor pointer
  #footer
    position fixed
    bottom 0
    left 0
    right 0
  .third-party
    position relative
    margin 15px 0
    text-align center
  .third-party-text
    position relative
    color #444
    font-size fm
  .third-party:before,.third-party:after
    content ''
    position absolute !important
    border-bottom 1px solid #666
    width 30%
    top 11px
  .third-party:before
    left 0
  .third-party:after
    right 0
  .third-party-btn
    margin-top 15px
    ul
      width 100%
      display flex
      justify-content center
      li
        width 50px
        height 50px
        margin 0 20px
        cursor pointer
  .third-party-btn li:first-child
    background-image  url(../common/img/login-logo.png)
    background-position -30px -30px
  .third-party-btn li:nth-child(2)
    background-image  url(../common/img/login-logo.png)
    background-position -122px -30px
  .third-party-btn li:nth-child(3)
    background-image  url(../common/img/login-logo.png)
    background-position -212px -31px
  .third-party-btn li:hover
    opacity .8
  .third-party-btn li a
    display block
    height 100%
  /* 小于767px */
  @media screen and (max-width: 767px){
    #login{
      position absolute !important
      bottom 1.3rem !important
      padding-bottom 0 !important
    }
    .login-main-wrapper {
      width 100%;
      box-sizing border-box;
    }
  }
</style>



