<template>
  <div id="productList">
    <productHeader></productHeader>
    <div class="all-classify container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>产品列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-row>
        <el-col :span="24"><div class="grid-content"><span>家纺</span> <span>商品筛选</span> <span>共2677个商品</span></div></el-col>
      </el-row>
      <ul class="classify-list">
        <li v-for="item in 4" :class="{active:moreActive == item}">
          <span class="classify-name">分类：</span>
          <ul>
            <li v-for="classify in 30">
              <span>
                被套
              </span>
            </li>
          </ul>
          <span class="more" @click="moreClick(item)">
            更多 <i class="el-icon-arrow-right"></i>
          </span>
        </li>
      </ul>
      <div class="small-img">
        <ul>
          <li v-for="item in 30">
            <router-link to="/productDetail">
              <img src="../../common/img/222.jpg" alt="" width="100%">
              <p>123萨科技厚大司考</p>
              <h6>¥ 399</h6>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="paging">
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next"
          :total="500">
        </el-pagination>
      </div>
    </div>
    <goTop></goTop>
    <productFooter></productFooter>
  </div>
</template>

<script type="text/ecmascript-6">
  import productHeader from './header'
  import productFooter from './footer'
  import goTop from './goTop'
  export default {
    components: {
      productHeader,
      productFooter,
      goTop
    },
    data() {
      return {
        currentPage: 1,
        moreActive:''
      };
    },
    methods: {
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      moreClick: function (item) {
        if (this.moreActive == item){
          this.moreActive = ""
        }else {
          this.moreActive = item;
        }
      }
    }
  };
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../../common/stylus/general.styl"
  *{transition: all .1s ease-in;}
  .all-classify
    margin-top 50px
  .el-row
    margin-top 20px
    background #ddd
  .grid-content
    font-size 16px
    color #333
    padding 5px
    span
      margin-right 10px
  .classify-list
    margin-bottom 20px
  .classify-name
    vertical-align top
    display inline-block
    padding-top 4px
  .classify-list>li.active
    height auto
  .classify-list>li
    position relative
    height 45px
    width 100%
    overflow hidden
    border-bottom 1px solid #ddd
    padding 10px
    padding-bottom 0
    box-sizing border-box
    ul
      width 91%
      overflow hidden
      display inline-block
      padding-top 2px
      padding-left 10px
    ul.active
      height auto !important
    li
      margin-right 30px
      float left
      font-size 14px
      color #666
      margin-bottom 10px
      cursor pointer
  .more
    position absolute
    right 10px
    font-size 14px
    cursor pointer
    top 13px
  .small-img
    overflow hidden
  .small-img li
    float left
    width 20%
    box-sizing border-box
    padding .1rem
    text-align center
    font-size 14px
    overflow hidden
    a
      display inline-block
      width 100%
      height 100%
      background #ddd
      overflow hidden
    p
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      margin 2px
    h6
      color color
      padding-bottom .1rem
  .small-img li img
    transition transform .3s ease-in
  .small-img li:hover img
    transform scale(1.05)
  .paging
    text-align center
    margin 30px 0
  @media screen and (max-width: 767px){
    .all-classify{
      padding-bottom 1.3rem
    }
    .all-classify .small-img li{
      width 50%
    }
    .classify-list>li ul{
      width 75%
    }
  }
</style>



