import axios from 'axios';
import router from '@/router'
import {getCookie,setCookie} from './cookie';
axios.defaults.timeout = 5000;
axios.defaults.baseURL ='';
var baseUrl = ''
if(process.env.NODE_ENV == 'development'){ // development // production
  baseUrl = '/api';
}

//http request 拦截器
axios.interceptors.request.use(
  config => {
    //将用户登录的x-user-lg值取出来并将值传给后台
    const token = getCookie('userLg');//注意使用的时候需要引入cookie方法，推荐js-cookie
    config.data = JSON.stringify(config.data);
    //   if(token[0] == '1'){
    //
    //   }

    config.headers = {
      'Content-Type':'application/x-www-form-urlencoded',
        // 'cookie':cookie
    }
    // if(token){
    //   config.params = {'token':token}
    // }
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);


//http response 拦截器
axios.interceptors.response.use(
  response => {
    //拿到状态码和x-user-lg，同时判断是否需要登录，将x-user-lg的值存在cookie
    if (response.headers["x-user-lg"]) {
      const cookie = response.headers["x-user-lg"].split(";");
      if (response.headers) {
        if (response.headers.responsecode == 1 && cookie[0] == 1) {
          router.push({
            path:"/login",
            querry:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
          })
        }
      }
    }
      //   if(cookie['UserLg'][0] != 1 && response.headers.get('x-user-lg') == cookie['UserLg']){
      //       // 更新cookie 里的user
      //   }
    if(response.data.errCode ==2){
      router.push({
        path:"/login",
        querry:{redirect:router.currentRoute.fullPath}//从哪个页面跳转
      })
    }
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)


/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(baseUrl+url,{
      params:params
    })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.post(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}

/**
 * 封装patch请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}

/**
 * 封装put请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(baseUrl+url,data)
      .then(response => {
        resolve(response.data);
      },err => {
        reject(err)
      })
  })
}



