<template>
  <div>
    <div class="page-head">
      <div class="jas-container">
        <h1 class="classify-name">BABY CLOTHING SETS</h1>
        <p>Baby Clothing Sets</p>
        <ul class="el-breadcrumb">
          <li><router-link to="/index_1">Home</router-link><span class="el-icon-arrow-right"></span></li>
          <li>Baby Clothing Sets</li>
        </ul>
      </div>
    </div>
    <div class="container">
      <div class="filter-search-wrap">
        <div class="filter-search">
          <div class="filter-wrap">
            <span class="pop_default" @click="filterClick">
              <i class="iconfont icon-third-filter"></i>
              Filters
            </span>
            <span class="sp_result_html">
              Showing all 5 results
            </span>
          </div>
          <div class="center-wrap">
            <ul class="sp-col-switch">
              <li class="one" @click="distribution('one')" :class="{active : distributionActive == 'one'}"></li>
              <li class="two" @click="distribution('two')" :class="{active : distributionActive == 'two'}"></li>
              <li class="three" @click="distribution('three')" :class="{active : distributionActive == 'three'}"></li>
              <li class="four" @click="distribution('four')" :class="{active : distributionActive == 'four'}"></li>
            </ul>
          </div>
          <div class="search-wrap">
            <div style="max-width:170px">
              <el-input placeholder="请输入内容" v-model="searchInput" class="input-with-select">
                <el-button slot="append" icon="el-icon-search"></el-button>
              </el-input>
            </div>
          </div>
        </div>
      </div>
      <div class="filter-pop" :class="{active : filterDefault}">
        <div class="filter-pop-section">
          <ul>
            <li v-for="item in 50">
              短袖
            </li>
          </ul>
        </div>
      </div>
      <div class="product-list-row container">
        <div class="product-list">
          <div class="product-list-item" v-for="item in 10">
            <router-link to="0">
              <img src="https://cdn.shopify.com/s/files/1/0017/3051/0960/products/HTB1ssSkm6nD8KJjSspbq6zbEXXa0_540x.jpg?v=1548666672" alt="">
              <p class="product-name">0-24M Baby Girl Turn-down Collar Long Sleeve Lace Blouse</p>
              <p class="product-price">¥42.49</p>
            </router-link>
          </div>
        </div>
        <div class="sidebar">
          <el-collapse accordion style="border: none">
            <el-collapse-item title="+ 短袖" v-for="item in 5" :name="item">
              <router-link to="00"><i>+</i> 短袖</router-link>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        distributionActive: 'four',
        searchInput: '',
        filterDefault:false
      }
    },
    methods: {
      filterClick: function(){
        this.filterDefault = !this.filterDefault;
      },
      distribution: function (type) {
        this.distributionActive = type
        if (type == 'one'){
          $(".product-list").removeClass('w2 w3 w4').addClass('w1')
        }else if (type == 'two'){
          $(".product-list").removeClass('w1 w3 w4').addClass('w2')
        }else if (type == 'three'){
          $(".product-list").removeClass('w1 w2 w4').addClass('w3')
        } else {
          $(".product-list").removeClass('w1 w3 w2').addClass('w4')
        }
      }
    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../../common/stylus/general.styl"
  *{transition: all .3s ease;font-family Helvetica, 'Helvetica Neue', Arial, 'Lucida Grande', sans-serif;}
  .page-head
    background url(../../common/img/listBackground.jpg) no-repeat center center
    background-size cover
    padding 50px 0
    color #fff
    text-align center
    position relative
    .jas-container
      position relative
      z-index 1
      h1
        font-size 20px
        font-weight 700
        margin-bottom 10px
      p
        margin 0
        font-family 'Libre Baskerville',serif
        font-style italic
        font-size 14px
      .el-breadcrumb
        display inline-block
        margin-top 20px
        li
          float left
          color #fff
          a
            color #fff
          span
            padding 0 2px
  .el-breadcrumb a:hover
    color color2 !important
  .page-head:before
    content ''
    position absolute
    left 0
    top 0
    right 0
    bottom 0
    background rgba(0,0,0,.5)
  .filter-search-wrap
    padding-top 50px
    color #999
  .filter-search
    display flex
    -webkit-box-pack justify
    justify-content space-between
    -webkit-box-align center
    align-items center
    font-size 14px
  .pop_default
    color #222
    transition all .3s
    cursor pointer
  .sp_result_html:before
    content '|'
    display inline-block
    margin 0 2px
  .center-wrap ul
    display flex
    li
      height 26px
      border 1px solid #878787
      position relative
      cursor pointer
      margin-right 10px !important
  .center-wrap li.one
    width 28px
  .center-wrap li.two
    width 29px
  .center-wrap li.three
    width 42px
  .center-wrap li.four
    width 55px
  .center-wrap li.one:before
    width 22px
  .center-wrap li.two:before
    box-shadow: 13px 0 0 #878787, 13px 0 0 #878787;
  .center-wrap li.three:before
    box-shadow: 13px 0 0 #878787, 26px 0 0 #878787;
  .center-wrap li.four:before
    box-shadow: 13px 0 0 #878787, 26px 0 0 #878787, 39px 0 0 #878787;
  .center-wrap li.two:hover:before
    box-shadow: 13px 0 0 #222, 13px 0 0 #222;
  .center-wrap li.three:hover:before
    box-shadow: 13px 0 0 #222, 26px 0 0 #222;
  .center-wrap li.four:hover:before
    box-shadow: 13px 0 0 #222, 26px 0 0 #222, 39px 0 0 #222;
  .center-wrap li.two.active:before
    box-shadow: 13px 0 0 #222, 13px 0 0 #222;
  .center-wrap li.three.active:before
    box-shadow: 13px 0 0 #222, 26px 0 0 #222;
  .center-wrap li.four.active:before
    box-shadow: 13px 0 0 #222, 26px 0 0 #222, 39px 0 0 #222;
  .center-wrap li.active:before
    background #222
  .center-wrap li:hover:before
    background #222
  .center-wrap li:before
    content ""
    position absolute !important
    top 3px
    left 3px
    width 10px
    height 20px
    background #878787
    transition all .3s
  .one
    display none
  .filter-pop
    height 0
    background #f1f1f1
    overflow auto
  .filter-pop.active
    height auto
    max-height 200px
    padding 30px 0
    margin-top 30px
  .filter-pop li
    float left
    margin 15px
    color #222
    font-size fm
    cursor pointer
  .filter-pop li:hover
    color color2
  .product-list-row
    display flex
  .product-list
    flex-basis 83.333%
    max-width 83.333%
    position relative
    overflow hidden
    margin 0 -15px
    transition opacity .25s ease,visibility .25s ease,transform .25s ease,-webkit-transform .25s ease
  .product-list-item
    float left
    padding 0 15px
    box-sizing border-box
    cursor pointer
    margin-top 30px
    overflow hidden
  .product-list-item:hover img
    transform scale(1.1)
  .product-list-item:hover .product-name
    color color2
  .product-list-item img
    width 100%
  .product-name,.product-price
    font-size fm
    color #4d5959
    text-align center
    margin-top 15px
  .sidebar
    width 16.666%
    padding-left 15px
    box-sizing border-box
    margin-top 30px
    margin-right 0
  .sidebar a
    margin-left 15px
    color #222
  .sidebar a:hover
    color color2
  .w4 .product-list-item
    width 25%
  .w3 .product-list-item
    width 33.333%
  .w2 .product-list-item
    width 50%
  .w1 .product-list-item
    width 100%
  @media screen and (max-width: 992px){
    .four{
      display none
    }
    }
  /* 小于767px */
  @media screen and (max-width: 767px){
    .product-list-item,.product-list{
      width 100%
    }
    .three,.four,.sp_result_html,.sidebar,.three{
      display none
    }
    .one{
      display block
    }
  }
</style>



