<template>
  <div id="findPassword">
    <div class="login-main-wrapper">
      <ul class="login-list">
        <li>找回密码</li>
      </ul>
      <div class="login-main">
        <el-form :model="passwordForm" ref="passwordForm" :rules="rules">
          <el-form-item prop="email">
            <el-input placeholder="邮箱" v-model="passwordForm.email">
              <template slot="prepend">邮箱</template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="login-button" :loading="loading" @click="loginBtn('passwordForm')" style="width: 100%;background: #42a749;border: none;font-size: .35rem;margin-top: 20px">确定</el-button>
          </el-form-item>
        </el-form>
        <div class="login-foot">
          <router-link to="/login">去登录或者注册账号</router-link>
        </div>
      </div>
    </div>
    <productFooter></productFooter>
  </div>
</template>

<script type="text/ecmascript-6">
  import productFooter from './style1/footer'
  export default {
    components: {
      productFooter
    },
    data() {
      return {
        loading:false,
        passwordForm: {
          email:'',
        },
        rules: {
          email:[
            {
              required: true,
              message: '请输入邮箱地址',
              trigger: 'blur'
            },
            {
              type: 'email',
              message: '请输入正确的邮箱地址',
              trigger: ['blur', 'change']
            }
          ]
        }
      }
    },
    methods: {
      loginBtn: function (login) {
        this.$refs[login].validate((valid) => {
          if (valid) {
            this.loading=true
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "../common/stylus/general.styl"
  #findPassword
    position relative
    top 0
    right 0
    left 0
    bottom 0
    padding-bottom 400px
    background url(../common/img/background.jpg) no-repeat;
    background-size 100% 100%
  .login-main-wrapper
    width 500px
    height 400px
    border 1px solid #ddd
    top 80px
    background rgba(250,250,250,.6)
  .login-main-wrapper
    width 500px
    height 400px
    border 1px solid #ddd
    top 80px
    background rgba(250,250,250,.6)
    .login-list
      width 100%
      height 50px
      margin-bottom 50px
      li
        height 100%
        box-sizing border-box
        border-bottom 1px solid #eee
        text-align center
        font-size fl
        color color
        line-height 50px
        font-weight bold
        background #fff
  .login-main
    width 80%
    margin 0 auto
  .login-foot
    font-size fm
    margin-top 50px
    text-align center
  #footer
    position fixed
    bottom 0
    left 0
    right 0
  /* 小于767px */
  @media screen and (max-width: 767px){
    #findPassword{
      position absolute !important
      bottom 1.3rem !important
      padding-bottom 0 !important
    }
    .login-main-wrapper {
      width 100%;
      box-sizing border-box;
    }
  }
</style>



